<template>
	<div class="index-board">
		<div class="inner">
			<h2>欢迎来到LittleBeeX</h2>
			<p class="node">
				当前节点：以太坊测试网(Ropsten)
				<span class="hint">需解锁Metamask钱包，并选择所需的服务器节点</span>
			</p>
			<div class="config-item">
				<label>创建一个新组织，并开始KYC组织认证</label>
				<Button type="primary" @click="createOrganization">开始创建</Button>
			</div>
			<div class="config-item">
				<label>打开现有组织</label>
				<p><Input v-model="companyName" size="large" :maxlength="20" type="text"/></p>
				<Button type="primary" @click="openOrganization" :disabled="!isClick" :loading="loading">打开组织</Button>
			</div>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		data(){
			return {
				companyName:'',
				loading: false,
				isClick: false
			}
		},
		methods:{
			openOrganization(){
				/* if(!/^[A-Za-z0-9]{1,20}$/.test(this.companyName)){
					this.$Notice.warning({
						title: '格式填写不正确',
						desc: false ? '' : ' '
					});
				} */
				let data = {"only":this.companyName};
				this.$axios({
					method: 'post',
					url: '/index.php/cn/home/node_se/company',
					data: Qs.stringify(data)
				}).then((response) => {
					console.log(response);
					/* if(){
						this.$Notice.warning({
							title: '暂无对应组织！',
						});
					}else{
						this.$router.push({path:'UserIdent'}) 
					} */
				})
			},
			createOrganization(){
				this.$router.push({path:'UserIdent'});
			}
		},
		watch:{
			companyName(){
				this.isClick = this.companyName == ''? false : true;
				console.log(this.isClick);
			}
		}
	}
</script>

<style scoped lang="stylus">
	.index-board
		padding: 50px 100px !important;
		h2
			font-size: 36px;
			margin-bottom: 40px;
			color: #333333;
		.node
			font-size: 20px;
			margin-bottom: 40px;
			.hint
				display: block;
				color: #bbbbbb;
				font-size: 14px;
		.config-item
			label
				display:block;
				margin: 30px 0;
				font-size: 20px;
			p
				display: inline-block;
</style>


